<template>
	<div>
		<h1 class="ui header">Home</h1>
		<input type="text" v-model="name">
		<div class="ui button" @click="greeting">greeting</div>
		<h3 class="ui header">{{message}}</h3>
	</div>
</template>

<script>
	export default {
		name: "Home",
		components: {},
		data() {
			return {
				name: '',
				message: ''
			}
		},
		methods: {
			greeting() {
				console.log("greeting");
				this.$SHARED.dispatchAsync(this.$SHARED.services.greeting, {name: this.name}).then( data => {
					this.message = data.message;
				}, () => {})
			}
		}
	}
</script>

